img {
    width: 100%;
}
.gallery{
    width: 80%;
    margin: 0 auto;
    text-align: center;
    .title{
        font-size: 3.2rem;
        color: white;
        padding: 0.8rem;
        font-weight: 200;
    }
    .back{
        padding: 1rem;
        margin: 1.6rem;
    }
    img{
        margin-bottom: 1.6rem;
    }
}
.grid {
    display: flex;
}

.grid-col {
    flex: 1;
}

.grid-col--2, .grid-col--3, .grid-col--4 {
    display: none;
}

@media (min-width: 768px) {
    .grid-col--2 { display: block; }
    .grid-col {
        padding: 0.8rem;
    }
    img {
        margin-bottom: 1.6rem;
    }
    .grid-item:hover {
        cursor: pointer;
    }
}

@media (min-width: 1080px) {
    .grid-col--3 { display: block; }
    .container {
        width: 80%;
    }
    h1 {
        margin: 5em 0;
    }
}