@import url('https://fonts.googleapis.com/css2?family=Libre+Barcode+128+Text&display=swap');
@import './_slider';
@import './gallery';

@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Courier New", Courier, monospace;
}

html {
    font-size: 62.5%;
}

body {
    background-color: rgb(0, 0, 0);
}

footer{
    color: white;
    text-align: center;
    height: 100%;
}

.introduction {
    @include flexCenter();
    height: 100vh;
    border: solid rgb(255, 255, 255) 2rem;
    flex-direction: column;
    color: white;
    h2 {
        font-size: 10.6rem;
        font-family: 'Libre Barcode 128 Text', cursive;
        font-weight: lighter;
        padding: 1.6rem;
        display: inline;
    }
}

#welcome-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100%;
}

.nav-icons{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    width: 80%;
    margin-top: 3.2rem;
    svg{
        cursor: pointer;        
    }    
}


//2000 wide or less
@media screen and (max-width: 2000px) {
    .introduction h2 {
        font-size: 6.4rem;
    } 
}
@media screen and (max-width: 1065px) {
    .introduction h2 {
        font-size: 5.6rem;
    } 
}
@media screen and (max-width: 800px){
    .introduction{
        border: none;
        height: 100vh;
        overflow: hidden; 
    }
    div.nav-icons{
        display: none;
    }
    .swiper-container{
        svg{
            scale: 80%;        
        } 
    }
}
//500 wide or more
@media screen and (min-width: 800px){
    div.swiper-container{
        display: none;
    }
}